.left-nav {
    background: rgb(56, 53, 53);
    padding-top: var(--size-large);
    z-index: 10;
    overflow: hidden;
}


@media (max-width: 720px) {
    .left-nav {
        display: none;
    }
}

.folders {
    display: flex;
    flex-direction: column;
    align-items: start;
    color: lightgray;
}

.level1 {
    display: flex;
    font-size: small;
    padding: var(--size-micro) var(--size-huge) 0 var(--size-medium);
    margin: var(--size-tiny) 0;
    align-items: center;
}

.level2 {
    display: flex;
    font-size: small;
    padding: var(--size-micro) var(--size-huge) 0 var(--size-largeish);
    margin: var(--size-micro) 0;
    align-items: center;
    width: 100%;
}

@media (max-width: 720px) {
    .level1 {
        margin: var(--size-small) var(--size-large) 0 var(--size-medium);
    }

    .level2 {
        margin: var(--size-small) var(--size-large) 0 var(--size-largeish);
    }
}

.selected {
    background: rgba(0, 72, 117, 255);
    border: solid rgba(9, 106, 174, 255) 0.5px;
}

svg {
    margin-right: var(--size-tiny);
}