.tech-icon {
    margin: var(--size-large);
    width: -webkit-fill-available;
    height: -webkit-fill-available;
}

.tech-card {
    background-color: transparent;
    width: 225px;
    height: 250px;
    font-size: var(--size-largeish);
    font-weight: bolder;
    padding: var(--size-small);
}


@media (max-width: 720px) {
    .tech-card {
        font-size: var(--size-large);
        width: 190px;
        height: 205px;
    }
}


.tech-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: left;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0, 0, 0, .16);
}

.tech-card:hover .tech-card-inner {
    transform: rotateY(180deg);
}

.tech-card-front,
.tech-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: var(--size-small);
}

.tech-card-front {
    background-color: rgb(104, 98, 98);
    display: flex;
    align-items: center;
    justify-content: center;
}

.tech-card-back {
    background-color: rgb(94, 87, 87);
    color: rgb(112, 174, 195);
    transform: rotateY(180deg);
}

.card-description {
    font-size: var(--size-medium-plus);
    padding: 0 var(--size-medium);
    font-weight: lighter;
}

.card-title {
    padding: var(--size-medium);
}

@media (max-width: 720px) {
    .card-description {
        font-size: var(--size-medium);
        padding: 0 var(--size-small);
    }

    .card-title {
        padding: var(--size-small);
    }
}

.card-group {
    display: flex;
    width: 100%;
}